    content: `
#  Mirage Basics - Les Bases

##  Pourquoi Mirage ?

Mirage est une carte **trs quilibre** entre Terrorists et Counter-Terrorists. Elle est excellente pour apprendre les stratgies d'quipe !

##  Plan de Mirage

###  Zones Principales :
- **A Site** : Objectif A avec les caisses
- **B Site** : Objectif B avec les appartements
- **Mid** : Centre de la carte
- **Connector** : Lien entre Mid et A
- **Ramp** : Monte vers A
- **Apartments** : Appartements vers B

###  Particularits :
- **Carte ferme** : Beaucoup de coins et d'angles
- **quilibre** : Pas d'avantage net pour une quipe
- **Stratgique** : Beaucoup de possibilits tactiques

##  Ct Terrorist (Attaquants)

###  Stratgies Simples :
1. **Rush B Apps** : Tous vers B par les appartements
2. **A Site Execute** : Attaque coordonne sur A
3. **Mid Control** : Contrler Mid puis Connector
4. **Split B** : Apps + Mid vers B

###  Routes d'Attaque :
- **Vers A** : T Spawn  Mid  Connector  A Site
- **Vers A** : T Spawn  Ramp  A Site
- **Vers B** : T Spawn  Apartments  B Site
- **Vers B** : T Spawn  Mid  Underpass  B Site

##  Ct Counter-Terrorist (Dfenseurs)

###  Setup Standard :
- **2 joueurs A** : 1 Site A + 1 Connector
- **2 joueurs B** : 1 Site B + 1 Apps
- **1 joueur Mid** : Contrle le centre

###  Rotations :
- **De A vers B** : Passe par Connector et Mid
- **De B vers A** : Passe par Underpass et Mid
- **Support Mid** : Aide selon les besoins

##  Positions Dtailles

###  A Site
**Terrorists** :
- **Ramp** : Monte directe vers A
- **Connector** : Passage depuis Mid
- **Conseils** : Utilise les smokes et flashbangs

**Counter-Terrorists** :
- **Site** : Derrire les caisses ou dans les coins
- **Connector** : Contrle le passage depuis Mid
- **Conseils** : Communique avec Mid

###  B Site (Apartments)
**Terrorists** :
- **Apps** : Appartements vers B
- **Underpass** : Passage depuis Mid
- **Conseils** : Attention aux CT qui peuvent tre partout

**Counter-Terrorists** :
- **Site B** : Dfend le site depuis diffrents angles
- **Apps** : Contrle les appartements
- **Conseils** : Utilise les grenades pour retarder

###  Mid (Centre)
**Terrorists** :
- **Mid** : Contrle essentiel pour les rotations
- **Top Mid** : Position leve
- **Conseils** : Smoke pour bloquer les snipers

**Counter-Terrorists** :
- **Mid** : Position centrale pour aider A et B
- **Connector** : Lien vers A Site
- **Conseils** : Trs importante pour les rotations

##  Conseils pour Dbutants

###  Bonnes Pratiques :
- **Smoke Mid** : Bloque la vue des snipers CT
- **Clear angles** : Regarde chaque coin avant d'avancer
- **Communique** : Dis o tu vois les ennemis
- **Utilisez les grenades** : Trs importantes sur Mirage

###  Erreurs Communes :
- Courir dans Apps sans regarder
- Ignorer Mid
- Ne pas utiliser de smokes
- Mauvaise communication

##  Stratgies Spcifiques

###  Rush B Apps (Facile)
**Plan** : Tous les 5 T vont vers B
**Execution** :
1. **Achetez** : Armor + SMG/rifles + grenades
2. **Smoke** : Bloc la vue depuis Mid
3. **Rush** : Tous ensemble dans Apps
4. **Clear** : Vrifiez chaque angle
5. **Plant** : Vite la bombe sur B

###  A Site Execute (Intermdiaire)
**Plan** : Attaque coordonne sur A
**Execution** :
1. **Setup** : 3 joueurs Ramp + 2 joueurs Connector
2. **Smokes** : Bloquez CT et Jungle
3. **Flash** : Aveuglent les dfenseurs
4. **Execute** : Attaque simultane
5. **Trade** : Aide tes coquipiers

###  Mid Control (Avanc)
**Plan** : Contrler Mid puis aider
**Execution** :
1. **Smoke Mid** : Bloque la vue CT
2. **Prends Top Mid** : Position importante
3. **Connector** : Aide l'attaque sur A
4. **Underpass** : Ou aide l'attaque sur B

##  Callouts Importants

###  Noms des Positions :
- **A Site** : Le site A
- **B Site** : Le site B  
- **Mid** : Le centre
- **Connector** : Lien Mid-A
- **Ramp** : Monte vers A
- **Apps** : Appartements
- **Underpass** : Passage sous Mid
- **Jungle** : Zone prs de A
- **CT Spawn** : Spawn des CT

###  Exemples de Communication :
- "Ennemi Connector !"
- "Ils rushent Apps !"
- "AWP Mid, smoke !"
- "Rotation B vers A !"

##  Grenades Importantes

###  Smokes Essentielles :
- **Mid** : Bloque la vue des CT
- **CT** : Bloque les rotations
- **Jungle** : Aide l'attaque A
- **Apps** : Aide l'attaque B

###  Flashbangs Utiles :
- **Ramp** : Aveugle les dfenseurs A
- **Apps** : Aveugle les dfenseurs B
- **Connector** : Aide les rotations

##  Objectif Final

Matriser Mirage te donne une **excellente base tactique** ! C'est la carte parfaite pour apprendre le jeu d'quipe.

**Prochaine tape** : Apprendre Inferno, une carte plus complexe !
`,
